#main {
  .h-entry {
    width: 100%;
  }
}

#about {
  margin-left: 1rem;
  margin-right: 1rem;
}

@media (min-width: 768px) {
  #about {
    margin-left: 2rem;
    margin-right: 2rem;
  }
}

@media (min-width: 1024px) {
  #about {
    margin-left: 4rem;
    margin-right: 4rem;
  }
}

#about {
  p {
    --tw-text-opacity: 1;
    color: rgb(241 242 238 / var(--tw-text-opacity));
  }
  p {
    text-indent: 1rem;
  }

}

#key-skills {
  ul {
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
  ul {
    display: flex;
  }
  ul {
    list-style-type: none;
  }
  ul {
    justify-content: center;
    flex-wrap: wrap;

    li {
      margin-left: 0.5rem;
      margin-right: 0.5rem;
    }

    li {
      margin-top: 0.5rem;
      margin-bottom: 0.5rem;
    }

    li {
      border-bottom-width: 2px;
    }

    li {
      --tw-border-opacity: 1;
      border-color: rgb(186 140 78 / var(--tw-border-opacity));
    }

    li {
      padding: 0.25rem;
    }

    li {
      font-size: 0.875rem;
      line-height: 1.25rem;
    }

    li {
      --tw-text-opacity: 1;
      color: rgb(186 140 78 / var(--tw-text-opacity));
    }

    @media (min-width: 768px) {
      li {
        margin-left: 1rem;
        margin-right: 1rem;
      }
    }

    @media (min-width: 768px) {
      li {
        margin-left: 1rem;
        margin-right: 1rem;
      }
    }

    @media (min-width: 768px) {
      li {
        margin-left: 1rem;
        margin-right: 1rem;
      }
    }

    @media (min-width: 768px) {
      li {
        padding: 0.5rem;
      }
    }

    @media (min-width: 768px) {
      li {
        font-size: 1.5rem;
        line-height: 2rem;
      }
    }
  }
}

.entry {
  margin-bottom: 2rem;

  .header {

    em {
      text-align: center;
    }
  }

  .header {
    border-bottom-width: 2px;
  }

  .header {
    --tw-border-opacity: 1;
    border-color: rgb(186 140 78 / var(--tw-border-opacity));
  }

  .header {
    font-size: 1rem;
    line-height: 1.5rem;
  }

  .header {
    --tw-text-opacity: 1;
    color: rgb(186 140 78 / var(--tw-text-opacity));
  }

  @media (min-width: 768px) {
    .header {
      font-size: 1.125rem;
      line-height: 1.75rem;
    }
  }

  .header {
    display: flex;
  }

  .header {
    flex-direction: column;
  }

  .header {
    align-items: center;
  }

  .header {
    justify-content: space-between;
  }

  @media (min-width: 768px) {
    .header {
      flex-direction: row;
    }
  }

  li {
    --tw-text-opacity: 1;
    color: rgb(241 242 238 / var(--tw-text-opacity));
  }
}

ul.enumeration {
  margin-left: 2rem;
  list-style-type: disc;

  li {
    --tw-text-opacity: 1;
    color: rgb(241 242 238 / var(--tw-text-opacity));
  }

  li {
    margin: 0;
  }
}
